<template>
  <div>
    <v-app>
      <v-container>
        <v-layout>
          <v-row>

            <h1>Users page</h1>



            <v-btn depressed small dark color="success" @click="spotSave">
              <v-icon>DownloadDES</v-icon>
            </v-btn>
            <v-spacer></v-spacer>
            <button v-on:click="blobSave">DownloadDES</button>                        
            <v-spacer></v-spacer>

          </v-row>
        </v-layout>  
      </v-container>
    </v-app>

  </div>

</template>

<script>
export default {
  name: "App",

  components: {},

  data: () => ({
    spot: [
      {
          x: "1",
          y: "1",
          z: "1",
      },
      {
          x: "2",
          y: "2",
          z: "2",
      },
      {
          x: "3",
          y: "3",
          z: "3",
      },                
    ]
  }),
  methods: {
    
    spotSave() {
      var str = '';
      for(let i = 0; i<this.spot.length; i++){
          str = str + this.spot[i].x + ',' + this.spot[i].y + ',' + this.spot[i].z + '\r\n';
      }
      const filename = 'spotDes.txt';
      const element = document.createElement('a');
      element.setAttribute('href', 'data:text/plain;charset=utf-8, ' + encodeURIComponent(str));
      element.setAttribute('download', filename);
      document.body.appendChild(element);
      element.click();
    },
    blobSave() {
      var str = '';
      for(let i = 0; i<this.spot.length; i++){
          str = str + this.spot[i].x + ',' + this.spot[i].y + ',' + this.spot[i].z + '\r\n';
      }
      const blob = new Blob([str], { type: 'text/plain' });
      let element = document.createElement('a');
      element.href = URL.createObjectURL(blob);
      element.download = 'blobDes.txt';
      element.click();
    },
  }
}
</script>

