import { createRouter, createWebHistory } from 'vue-router'

import HomePage from '../views/HomePage'
import UsersPage from '../views/UsersPage'

const router = createRouter({
  history: createWebHistory(),
  routes: [
    {
        path: '/',
        name: 'Home',
        component: HomePage
      },
      {
        path: '/user',
        name: 'Users',
        component: UsersPage
      } 
  ]
})

export default router